body {
  background-color: #1e1e1e;
  color: white;
}
header {
  text-align: center;
  background-color: black;
  padding: 50px 0 150px 0;
}
header i {
  color: #5e60ce;
}
header span:first-of-type {
  color: #4ea8de;
}
header span:last-of-type {
  color: #1e6f9f;
}
main {
  max-width: 600px;
  width: calc(100% - 4rem);
  margin: auto;
}
footer {
  text-align: center;
  padding: 2rem;
  color: lightgray;
}

.inputArea {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  transform: translateY(-50%);
}
.inputArea > button,
.inputArea > input {
  border: none;
  outline: none;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  color: white;
}

.inputArea > input {
  background-color: lightslategray;
  flex: 1;
}
.inputArea > button {
  background-color: #4ea8de;
}

.count {
  padding: 1rem;
  border-bottom: 1px dotted lightgray;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
}
.count span {
  background-color: aliceblue;
  padding: 0.5rem;
  border-radius: 0.5rem;
  color: #5e60ce;
}

.listCon {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding-top: 2rem;
}
.listCon > li {
  border: 1px solid lightgray;
  padding: 1rem;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.listCon > li:hover {
  background-color: #999;
}
.listCon > li button {
  background-color: transparent;
  border: none;
  outline: none;
  padding: 1rem;
  color: white;
}
.listCon > li button:hover {
  background-color: #eaeaea;
  color: black;
}

.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  backdrop-filter: blur(5px);
}

.modal ._inner {
  max-width: 600px;
  width: calc(50%);
  margin: 100px auto 0 auto;
  background-color: white;
  color: black;
  border-radius: 1rem;
  padding: 2rem;
  position: relative;
}

.modal ._inner button {
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
}
